.container {
  display: flex;
  position: relative;

  .startPoint {
    user-select: none;
    position: absolute;
    width: 50px;
    height: 50px;
    border-top: 1px solid lightgray;
    border-left: 1px solid lightgray;
    padding: 5px;
    color: lightgray;
  }

  .controls {
    display: flex;
    flex-direction: column;
    height: 100px;
    width: 0;
    position: relative;

    & > * {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      position: absolute;
      top: 20px;
      background: white;
      border-radius: 5px;
      border: 1px solid lightgray;
      box-shadow: 3px 3px 12px 0 rgba(0,0,0,0.35);
      cursor: pointer;
    }
  }

  .startControls {
    & > * {
      left: 20px;
    }

    z-index: 1;
  }

  .endControls {
    & > * {
      left: -60px;
    }
  }
}

.sidebarTab {
  display: flex;
  flex-direction: column;
  padding: 20px;

  & > * {
    margin-bottom: 20px !important;
  }

  & > *:last-child {
    margin-bottom: 0 !important;
  }

  .moods {
    display: flex;
    flex-direction: column;
    background: #eee;
    padding: 10px 20px;
    border-radius: 16px;
    max-height: 250px;
    overflow: scroll;
  }
}

.mood {
  display: flex;
  justify-content: space-between;
  padding: 20px;

  & > * {
    display: flex;
    align-items: center;
  }

  svg {
    width: 25px;
    height: 25px;
    cursor: pointer;

    path {
      fill: gray;
    }
  }
}

.moveArea {
  width: 100vw;
  height: 100vh;
  cursor: move;
}