.block {
  border-radius: 5px;
  border: 1px solid lightgray;
  box-shadow: 3px 3px 12px 0 rgba(0,0,0,0.35);

  .container {
    display: flex;

    .draggable {
      border-radius: 5px 0 0 5px;
      padding: 10px;
      border-right: 1px solid lightgray;
      background-color: #eee;

      svg {
        width: 30px;
        height: 30px;

        path {
          fill: gray;
        }
      }
    }

    .circleContainer {
      width: 0;
      display: flex;
      align-items: center;
      position: relative;

      .circle {
        width: 26px;
        height: 26px;
        border-radius: 50%;
        border: 1px solid lightgray;
        position: absolute;
        background: white;
        left: -13px;
        cursor: pointer;
      }

      .activeCircle {
        background-color: lightcyan;
      }
    }
  }
}

/*костыль чет*/
h5 {
  user-select: none;
}