.content {
  width: 400px;
  display: flex;
  flex-direction: column;
  padding: 20px;

  & > * {
    margin-bottom: 20px !important;
  }

  & > *:last-child {
    margin-bottom: 0 !important;
  }
}

.oddPadding {
  padding: 10px 20px 20px 20px;

  & > *:first-child {
    margin-bottom: 10px !important;
  }
}

.noPadding {
  width: auto;
  padding: 0;
}

.mood {
  padding: 20px;
  display: flex;
  justify-content: space-between;

  .circle {
    width: 26px;
    height: 26px;
    border-radius: 50%;
    border: 1px solid lightgray;
    background: white;
    cursor: pointer;
    position: absolute;
    right: -13px;
  }

  .activeCircle {
    background-color: lightcyan;
  }
}

.leftGroup {
  display: flex;
  align-items: center;

  & > *:first-child {
    margin-right: 10px;

    cursor: pointer;
  }
}