.link {
  position: absolute;
  width: 100px;
  height: 100px;
  background: linear-gradient(to top right,
          rgba(0,0,0,0) 0%,
          rgba(0,0,0,0) calc(50% - 0.8px),
          rgba(0,0,0,1) 50%,
          rgba(0,0,0,0) calc(50% + 0.8px),
          rgba(0,0,0,0) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;

  .close {
    width: 26px;
    height: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: indianred;
    cursor: pointer;
    pointer-events: auto;

    svg {
      width: 20px;
      height: 20px;

      path {
        fill: white;
      }
    }
  }
}